:root {
  --primaryBlue: hsl(208, 100%, 34%);
  --darkBlue: hsl(207, 100%, 13%);
  --medBlue: hsl(208, 57%, 47%);
  --lightBlue: hsl(195, 66%, 71%) ;
  --mutedBlue: hsl(208, 33%, 60%) ;
  --primaryGreen: hsl(92, 51%, 51%);
  --darkGreen: hsl(92, 50%, 20%);
  --medGreen: hsl(92, 51%, 66%);
  --lightGreen: hsl(79, 45%, 24%);
  --mutedGreen: hsl(92, 37%, 71%);
  --primaryOrange: hsl(32, 94%, 60%);
  --darkOrange: hsl(32, 62%, 42%);
  --medOrange: hsl(33, 94%, 68%);
  --lightOrange: hsl(32, 95%, 84%);
  --mutedOrange: hsl(32, 61%, 76%);
  --primaryYellow: hsl(49, 100%, 50%);
  --darkYellow:	hsl(49, 100%, 30%);
  --medYellow: hsl(49, 100%, 65%);
  --lightYellow: hsl(49, 100%, 80%);
  --mutedYellow: hsl(48, 54%, 70%);
  --primaryRed: hsl(347, 100%, 45%);
  --darkRed: hsl(347, 100%, 27%);
  --medRed:	hsl(347, 83%, 56%);
  --lightRed: hsl(347, 82%, 78%);
  --mutedRed: hsl(347, 54%, 67%);
  --weFinancial: hsl(193, 100%, 42%);
  --weFinancialDark: hsl(207, 100%, 26%);
  --lightGrey: hsl(0, 0%, 81%);
  --medGrey: hsl(0, 0%, 53%);
  --dkGrey: hsl(0, 0%, 25%);
  --black: hsl(0, 0%, 6%);
  --textBlack: hsl(0, 0%, 16%);
  --white: hsl(45, 22%, 96%);
  --white60: hsla(240, 100%, 99%, .85);
}

:root {
  --font-heading: Helvetica, Arial, sans-serif; /* heading font */
  --font-text: "Atkinson Hyperlegible", Helvetica, Arial, sans-serif; /* body font */
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-heading);
  line-height: 1.1;
  margin: 0 0 1.25rem;
}

h1 {
  font-size: 2.5rem;
  font-size: clamp(2.5rem, 0.9375rem + 3.35vw, 3.5rem);
}

h2 {
  font-size: 1.875rem;
  font-size: clamp(1.875rem, 1.6875rem + 0.78vw, 2.625rem);
  font-weight: 500;
}

h3 {
  font-size: 1.25rem;
  font-size: clamp(1.25rem, 1.1875rem + 0.26vw, 1.5rem);
  font-weight: 500;
  margin-bottom: 0;
}

p {
  font-size: 1rem;
  font-weight: 400;
  font-style: normal;
  margin-top: 0;
  line-height: 1.5rem;
}

.large {
  font-size: 1.25rem;
  font-weight: 400;
  font-style: normal;
  margin-top: 0;
  line-height: 1.625rem;
}

.small {
  font-size: 0.75rem;
  font-weight: 400;
  font-style: normal;
  margin-top: 0;
  line-height: 1.5rem;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
  background-color: var(--white);
  color: var(--black);
}

img {
  display: block;
  max-width: 100%;
}

figure,
blockquote {
  margin: 0;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.visually-hidden:focus {
  left: 0;
  width: auto;
  height: auto;
  background-color: #fff;
  padding: 0.5rem;
  border: 1px solid;
}